@media (min-width: 300px)  {   
  #iconAgent
  {
    width: 164px;
    height: 164px;
    background: #15328A 0% 0% no-repeat padding-box;
    border: 4px solid #0D214B;
    z-index: 1;
    opacity: 1;
    position: absolute;
    left: 50%;
    margin-left: -70px;
    position: fixed;
    top: calc(22% + (3vw/2));
    padding: 2px; 

} 
}
#picAgent
{
  margin-top: 70%;
    margin-left: 60%;
    width: 58px;
    height: 58px;
    background:#15328A 0% 0% no-repeat padding-box;
    border: 2px solid #0D214B;
    opacity: 1;
    z-index: 1;

}
#picAgentDesktop
{
  margin-top: 70%;
    margin-left: 60%;
    width: 58px;
    height: 58px;
    background:#15328A 0% 0% no-repeat padding-box;
    border: 2px solid #0D214B;
    opacity: 1;
    z-index: 2;

}